<template>
	<view>
		<uni-nav-bar class="nav-style" left-icon="back" right-icon="loop" :title="patient.name" color="#ffffff" background-color="#51D3C7" @clickLeft="toPatientList" @clickRight="reflesh"></uni-nav-bar>
		<view class="content">
			<view class="table-list">
				<view class="table-line table-title">
					<text class="table-item">记录时间</text>
					<text class="table-item">收缩压</text>
					<text class="table-item">舒张压</text>
					<text class="table-item">静脉压</text>
					<text class="table-item">跨膜压</text>
					<text class="table-item">脉搏</text>
				</view>
				<view class="table-line table-cnt" v-for="item in dialysisParamInfo">
					<text class="table-item">{{item.create_date}}</text>
					<text class="table-item">{{item.systolic_pressure}}</text>
					<text class="table-item">{{item.diastolic_pressure}}</text>
					<text class="table-item">{{item.venous_pressure}}</text>
					<text class="table-item">{{item.transmembrane_pressure}}</text>
					<text class="table-item">{{item.cardiotach}}</text>
				</view>
				<view class="btn-line table-cnt">
					<button class="table-btn" @click="addInfo">
						<uni-icons class="btn-icon" type="plus" color="#51D3C7" size="15"></uni-icons>
						新增
					</button>
				</view>
			</view>
		</view>		
	</view>

</template>

<script>
	export default {
		data() {
			return {
				patient:{},
				dialysisParamInfo:[],
			}
		},
		onShow: function () {
			this.patient = uni.getStorageSync("patient");
			//获取透析参数
			this.$myRequest({
				url:'/patient/dialysisparam',
				method:'POST',
				data:{
					cure_id:this.patient.cure_id,
				},
				success: (res) => {
					if(res.data.code == 200){
						console.log("透析参数",res.data.data);
						this.dialysisParamInfo = res.data.data;
					}
				},
			});
		},		
		methods: {
			//导航返回按钮对应方法
			toPatientList(){
				uni.navigateTo({
					url: "../patient-list/patient-list",
				});
				this.patient={};
				this.dialysisParamInfo=[];
			},
			//导航刷新按钮对应方法
			reflesh(){
				uni.showToast({
					title: 'loading',
					icon: 'loading',
					mask: true
				});
				//获取透析参数
				this.$myRequest({
					url:'/patient/dialysisparam',
					method:'POST',
					data:{
						cure_id:this.patient.cure_id,
					},
					success: (res) => {
						if(res.data.code == 200){
							console.log("透析参数",res.data.data);
							this.dialysisParamInfo = res.data.data;
						}
					},
				});
			},
			addInfo(){
				uni.navigateTo({
					url: "../dialysis-param-add/dialysis-param-add"
				});
			}
			
		}
	}
</script>

<style>
	@import url("../../static/css/style.css");
	.table-list{
		width: calc(100% - 4rpx);
		border: 2rpx solid;
		border-color: #C0C0C0;
		border-radius: 20rpx;
	}
	.table-line{
		width: 100%;
		height: 100rpx;
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}
	.table-title{
		color: #FFFFFF;
		font-weight: 600;
		background-color: #C0C0C0;
		border-radius: 20rpx 20rpx 0 0;
	}
	.table-cnt{
		border-top: 2rpx solid;
		border-color: #C0C0C0;
	}
	.table-item{
		flex: 1;
		text-align: center;
		font-size: 28rpx;
	}
	.btn-line{
		width: 100%;
		height: 100rpx;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.table-btn{
		width: 240rpx;
	}
	
</style>
